import { Layout, Menu, Image,Divider, Popconfirm, Modal } from 'antd';
import {
    SettingOutlined,
    ContainerOutlined,
    TeamOutlined,
    ExportOutlined,
    HomeOutlined,
    LoginOutlined,
    UserOutlined,
    BankOutlined,
    ExperimentOutlined,
    ReconciliationOutlined,
    AuditOutlined
} from '@ant-design/icons';
import {GET_ROLES} from '../graphql/auth'
import { useQuery } from '@apollo/client';
import Logo from '../asset/srLogo.png'
import { Link } from 'react-router-dom'
import { useContext, useState } from 'react';
import { HeaderData } from '../context/headerContext';
// import { auth } from '../api/firebase';
import { isLoggedInVar,rolesVar } from '../cache';
import { keyMenu } from '../function/fn'
// import AuthRoute from '../component/auth/AuthRoute'

const { Sider } = Layout;
const { SubMenu } = Menu;
const { confirm } = Modal;



export default function MenuHeader() {
    const { urlPath, user } = useContext(HeaderData)
    const [collapsed, setCollapsed] = useState(false)

    const { data: {roles} } = useQuery(GET_ROLES);



    const handleLoggout = ()=>{
        localStorage.removeItem("user")
        localStorage.removeItem("roles")
        isLoggedInVar(false)
        rolesVar([])
        window.location.reload()
    }

    function showConfirm() {
        confirm({
            title: '?',
            okType: 'danger',
            okText: "",
            cancelText: "",
            onOk() {
                handleLoggout();
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }

    const authRoute = (roles=[], setRoles=[],desc,title,icon) => {
        
        const  authenticated =(array1, array2) =>{
          
            for(let i = 0; i < array1.length; i++) {
                for(let j = 0; j < array2.length; j++) {
                    if(array1[i] === array2[j]) {
                        return true;
                    }
                }
            }
            return false; 

       
        }

        const auth = authenticated(roles,setRoles);
        return  auth && <Menu.Item key={`${desc}`} style={{fontSize:16}} icon={icon}>  <Link to={desc} /> {title}</Menu.Item>
      
            // <Menu.Item key={`${desc}`} style={{fontSize:16}}> {title} fhello <Link to={desc} /> </Menu.Item>}
     
   
    }
    
   
    return (
        <Sider collapsible collapsed={collapsed} onCollapse={() => setCollapsed(!collapsed)}
        className="side-custom"
            style={{
                // backgroundColor: '#06385f' 
                // backgroundColor: '#22252A' 
                // backgroundColor:'#1e3b87'
                // backgroundColor:'#005438'
                // background: 'repeating-linear-gradient(-55deg,#005b3d,#005b3d 30px,#005438 30px,#005438 40px)'
                // background: 'url("../asset/covid-19-bg.png") repeat center'

            }}>
            <Link to="/">
                <div className="logo">
                    <Image width={"100%"} className="img-logo" src={Logo} preview={false} />
                </div>
            </Link>
            <Divider style={{background:'#FFF',height:2}} />
            <Menu theme="dark"
                // defaultSelectedKeys={[urlPath]} 
                selectedKeys={[keyMenu(urlPath)]}
                mode="inline"
                style={{
            
                }}>

                    {authRoute(roles,["BASIC","DOCTOR","ADMIN"],"/","",<HomeOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(roles,["BASIC","DOCTOR","ADMIN","SUPER"],"/sample","",<ExperimentOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(roles,["BASIC","POLICE","ADMIN"],"/interview"," Covid-19",<AuditOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(roles,["BASIC","DOCTOR","ADMIN"],"/hospital","",<ReconciliationOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(roles,["BASIC","POLICE","ADMIN","SUPER"],"/quarantine","",<BankOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(roles,["BASIC","POLICE","ADMIN"],"/specifylocation","/",<AuditOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(roles,["BASIC","SUPPER","ADMIN"],"/people","",<TeamOutlined style={{fontSize:20}}  /> ) }

                <SubMenu key="sub1" style={{fontSize:16}} icon={<ContainerOutlined style={{fontSize:20,color:'#DDDD'}}  />} title={<span style={{color:'#DDDD'}}></span>}>
                    {authRoute(roles,["BASIC","SUPPER","ADMIN"],"/reportDaily","",<TeamOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(roles,["BASIC","SUPPER","ADMIN"],"/reportExcel"," EXCEL",<TeamOutlined style={{fontSize:20}}  /> ) }
    
                </SubMenu>
                    {authRoute(roles,["ADMIN"],"/case","",<LoginOutlined style={{fontSize:20}}  /> ) }
                    {authRoute(roles,["ADMIN"],"/user","",<UserOutlined style={{fontSize:20}}  /> ) }

                    <Menu.Item
                        // onClick={() => auth.signOut()}
                        style={{fontSize:16}} 
                        key="6"
                        icon={<ExportOutlined style={{fontSize:20}}  />}
                        onClick={showConfirm}
                        // onClick={() => handleLoggout()}
                    >
                        
                    </Menu.Item>
            </Menu>
        </Sider>
    )
}