import React, {useState} from 'react'
import { Table, Modal, Row, Col, Button, Select, Divider, Form, Input, DatePicker} from 'antd'
import { relatedCol } from '../tableColumn/relatedColumn'
import { ListSelect } from '../../../static/own-comp'
import { convertToCommune, convertToDistrict, convertToVillage, convertPersonalToSelect } from '../../../function/fn'
import { provinceData, districtData, communeData, villageData, genderData, nationalityData } from '../../../context/headerContext'

const {Option} = Select

export default function QuarantineRelated({open, setOpen}) {
    let [form] = Form.useForm()

    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")
    const [personalInfoIdRelated, setPersonalInfoIdRelated] = useState("")

    const onFinish = (values) => {

    }

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToGenderFn = (e) => {
        form.setFieldsValue({
            gender: e
        });
    };

    const setToPersonalInfoFn = (e) => {
        form.setFieldsValue({
            personalInfoIdRelated: e,
        });

        setPersonalInfoIdRelated(e);
      };

    const setToNationalityFn = (e) => {
        form.setFieldsValue({
            nationality: e
        });
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };


    const handleRelatedDelete =()=>{

    }

    return (
        <Modal
            width={800}
            visible={open}
            title="?"
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addPeople"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{ span: 24, offset: 0 }}
                    >
                        <Form.Item
                        name="personalInfoIdRelated"
                        rules={[{ required: true, message: "!" }]}
                        >
                        <ListSelect
                            type={6}
                            data={convertPersonalToSelect([])}
                            title=""
                            setValue={setToPersonalInfoFn}
                        />
                        </Form.Item>
                    </Col>

                    {personalInfoIdRelated === "new" ? (
                        <>
                        <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="idCard"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="patientId"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="lastName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="firstName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="englishName"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="dob"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="gender"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <ListSelect type={0} data={genderData} title="" setValue={setToGenderFn} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="age"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="occupation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="tel"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" addonBefore="+855" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="nationality"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={0} data={nationalityData} title="" setValue={setToNationalityFn} />
                        </Form.Item>
                    </Col>



                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "" ? (
                        <>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 24 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    {/* <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

                    {/* <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vaccinated"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col> */}

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="chronic"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    
                    <Divider></Divider>
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="times"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="vaccineType"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value="SINOPHARM">SINOPHARM</Option>
                                <Option value="SINOVAC">SINOVAC</Option>
                                <Option value="ASTRAZENECA">ASTRAZENECA</Option>
                                <Option value="JOHNSON JOHNSON">JOHNSON JOHNSON</Option>
                                <Option value="PFIZER">PFIZER</Option>
                                <Option value="Other">Other</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vacinatedAt"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                        </>
                    ):null}
                    

                    <Divider></Divider>
                   
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="direct"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>
                        <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="otherAffect"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    

                    {/* <Divider></Divider>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="case"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <ListSelect type={4} data={allCases} title="" setValue={setToCaseFn} disabled={caseId === "new" ? true : false} />
                        </Form.Item>
                    </Col>

                    {
                        caseId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="caseName"
                                >
                                    <Input disabled={true} style={{ backgroundColor: "white", color: "black" }} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }

                    <Col xs={24} md={caseId === "new" ? { span: 24 } : { span: 11, offset: 2 }}>
                        <Form.Item
                            name="direct"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>

                            </Select>
                        </Form.Item>
                    </Col>

                    

                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="other"

                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}


                    {/* <Col xs={24}>
                        <Form.Item
                            name="interviewed"
                            valuePropName="checked"
                        >
                            <Checkbox></Checkbox>
                        </Form.Item>
                    </Col> */}


                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ 
                                width: "100%",
                            }}
                        >
                            
                        </Button>
                    </Col>
                    <Col
                        xs={24}
                        style={{
                            marginTop: 20
                        }}
                    >
                        <Table
                            className="table-personal"
                            columns={relatedCol({ handleRelatedDelete })}
                            dataSource={[]}
                            rowKey={record => record.id}
                            pagination={false}
                            // pagination={{
                            //     total: subCasePagination?.totalDocs,
                            //     // showSizeChanger: true,
                            //     onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                            // }}
                            scroll={{ x: 1000, y: 300 }}
                            sticky
                        />
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
